import { Loading } from 'notiflix/build/notiflix-loading-aio';

//   
const loaderOptions = {
  svgColor: '#4f2ee8', //   
  backgroundColor: 'rgba(0,0,0,0.5)', //   
  svgSize: '80px', //  
};

//  
function startLoader() {
  Loading.circle('L o a d i n g', loaderOptions);
}

//  
function stopLoader() {
  Loading.remove();
}

//    
export { startLoader, stopLoader };
