import axios from 'axios';
import { startLoader, stopLoader } from './loader';

// URL  axios
axios.defaults.baseURL = 'https://books-backend.p.goit.global/books/';

//,      (    )
//     18- ' (18 ).   '    "list_name" -     .
async function fetchBooksCategoryList() {
  try {
    const { data } = await axios('category-list');
    return data;
  } catch (error) {
    console.log(error);
  }
}

//,        (   Best Sellers Books List   ).
//     18 ' (18 ).   '   "list_name" ( )   "books",   5 ' (5 ).    '   :
//"_id" - id  (   ,           )
//"author" -  
//"book_image" -    
//"title" -  
async function fetchTopBooks() {
  try {
    startLoader();
    const { data } = await axios('top-books');
    stopLoader();
    return data;
  } catch (error) {
    console.log(error);
  }
}

//,      (           "See more")
//    category -   .       (   "+"  "%20").
//       ' ().   10-15 .   '   :
//"_id" - id  (   ,           )
//"author" -  
//"book_image" -    
//"title" -  
async function fetchBooksByCategory(category) {
  try {
    startLoader();
    const { data } = await axios('category', {
      params: {
        category,
      },
    });
    stopLoader();
    return data;
  } catch (error) {
    console.log(error);
  }
}

//,       (        )
//    bookId
//    ',     :
//"book_image" -    
//"author" -  
//"title" -  
//"description" -  
//  "buy_links"  '     ,   .   '  "url" -       .   3  -  Amazon, Apple Books  Bookshop.

async function fetchBookById(bookId) {
  try {
    startLoader();
    const { data } = await axios(`${bookId}`);
    stopLoader();
    return data;
  } catch (error) {
    console.log(error);
  }
}

//    
export {
  fetchBooksCategoryList,
  fetchTopBooks,
  fetchBooksByCategory,
  fetchBookById,
};
